<template>
  <div>
    <h1><a href="https://www.jb51.net/article/137278.htm">hi</a></h1>
    <h1><a href="https://blog.csdn.net/java_green_hand0909/article/details/78740765">CORS 详解</a></h1>
    <button @click="getList">getList</button>
    <div style="margin-top: 20px;">
      <el-table ref="multipleTable"
                :data="citylist"
                :row-style="tableRowStyle"
                :header-cell-style="tableHeaderColor"
                tooltip-effect="dark"
                border fit highlight-current-row style="width: 100%;"
                height="600"
                row-key="id">
        <el-table-column width="center" align="center" label="ID" prop="ID"></el-table-column>
        <el-table-column width="center" align="center" label="Name" prop="Name"></el-table-column>
        <el-table-column align="center" label="CountryCode" prop="CountryCode"></el-table-column>
        <el-table-column align="center" label="District" prop="District"></el-table-column>
        <el-table-column align="center" label="Population" prop="Population"></el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'time',
  data () {
    return {
      listQuery: {
        ID: null,
        Named: null
      },
      citylist: [] // 建一个空数组，用来保存调用接口获取的数据
    }
  },
  created () {
    this.getList()
  },
  methods: {
    tableRowStyle ({ row, rowIndex }) {
      return 'background-color: #D2E9FF'
    },
    tableHeaderColor ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return 'background-color: #2894FF ;color: #fff;font-weight: 500;'
      }
    },
    getList () {
      this.$http.get('http://192.168.1.105:3000').then(res => {
        console.log(res)
        this.citylist = res.data
      })
    }
  }

}
</script>

<style scoped>

</style>



